  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  background-color: #e0f2fe;
}

body {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* header */

header {
  width: 100%;

  padding: 1rem 3rem;
  display: grid;
  grid-template-columns: 5rem 1fr 5rem;
  grid-template-rows: 2.5rem 1rem;
  align-content: center;
  column-gap: 5rem;
}

form {
  display: grid;
  grid-template-columns: 1fr 5rem;
  column-gap: 1rem;
}

form input {
  height: 2.5rem;
  font-size: 1rem;
  padding: 0 7px;
}

.suggestions {
  grid-column: 1 / 2;
  position: relative;
  display: flex;
  flex-direction: column;
}

.suggestions button {
  height: 2.5rem;
  padding: .5rem;
  text-align: left;
  font-size: 1rem;
  background-color: white;
}

.suggestions button:hover {
  background-color: lightgrey;
}

.suggestions button:active {
  background-color: gray;
}

.suggestions button:not(:first-child) {
  border-top: none;
}

.error {
  display: none;

  grid-column: 2 / 3;

  font-size: 1rem;
  color: red;
}

header > button,
form > button{
  background-color: #38bdf8;
  border: none;
  border-radius: .5rem;
}

header > button:hover,
form > button:hover{
  background-color: #0ea5e9;
}

header > button:active,
form > button:active{
  background-color: #0284c7;
  border: none;
  border-radius: .5rem;
}

/* content */

.content {
  width: 100%;
  max-width: 50rem;

  display: flex;
  flex-direction: column;
  gap: 2rem;

  flex: 1 1 auto;
}

/* Today's Weather */

.today {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  align-items: center;
  text-align: center;
  padding: 2rem;
}

.temperature {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.temperature-range {
  display: flex;
  flex-direction: row;
  gap: 1rem;
}

.subsections {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  align-items: center;
  gap: 1rem;
}

.subsection {
  height: 10rem;
  padding: 1rem;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 0.5rem;
}

.today h4 {
  font-size: 1.5rem;
}

.location h2 {
  font-size: 2rem;
}

.location h3 {
  font-size: 1rem;
}

.condition p {
  font-size: 1.5rem;
}

.temperature h5 {
  font-size: 3rem;
}

.temperature-range p {
  font-size: 1rem;
}

.subsections p {
  font-size: 1rem;
}

/* Hourly Forecast */
.hourly {
  padding: 1rem;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.hourly > div {
  padding: 0.5rem;
  display: grid;
  grid-template-columns: repeat(26, 8rem);
  grid-auto-flow: column;
  gap: 1rem;
  overflow-x: scroll;
  justify-items: center;

  scrollbar-width: thin;
  scrollbar-color: #1e293b #38bdf8;
}

.hourly > div > div {
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* Daily Forecast */
.forecast {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  align-items: center;
  gap: 1rem;
}

.forecast > div {
  padding: 1rem;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 0.5rem;
}

/* Footer */
footer {
  padding: 0.5rem;
  margin-right: auto;
}

footer p {
  font-size: 0.75rem;
}

footer a {
  text-decoration: none;
}

/* Style */
.today,
.hourly,
.forecast > div {
  border-radius: 1rem;
  background-color: #38bdf8;
  box-shadow: 10px 5px 5px gray;
}`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAAA;EACE,SAAS;EACT,UAAU;EACV,sBAAsB;AACxB;;AAEA;EACE,yBAAyB;AAC3B;;AAEA;EACE,iBAAiB;EACjB,aAAa;EACb,sBAAsB;EACtB,mBAAmB;AACrB;;AAEA,WAAW;;AAEX;EACE,WAAW;;EAEX,kBAAkB;EAClB,aAAa;EACb,oCAAoC;EACpC,+BAA+B;EAC/B,qBAAqB;EACrB,gBAAgB;AAClB;;AAEA;EACE,aAAa;EACb,+BAA+B;EAC/B,gBAAgB;AAClB;;AAEA;EACE,cAAc;EACd,eAAe;EACf,cAAc;AAChB;;AAEA;EACE,kBAAkB;EAClB,kBAAkB;EAClB,aAAa;EACb,sBAAsB;AACxB;;AAEA;EACE,cAAc;EACd,cAAc;EACd,gBAAgB;EAChB,eAAe;EACf,uBAAuB;AACzB;;AAEA;EACE,2BAA2B;AAC7B;;AAEA;EACE,sBAAsB;AACxB;;AAEA;EACE,gBAAgB;AAClB;;AAEA;EACE,aAAa;;EAEb,kBAAkB;;EAElB,eAAe;EACf,UAAU;AACZ;;AAEA;;EAEE,yBAAyB;EACzB,YAAY;EACZ,oBAAoB;AACtB;;AAEA;;EAEE,yBAAyB;AAC3B;;AAEA;;EAEE,yBAAyB;EACzB,YAAY;EACZ,oBAAoB;AACtB;;AAEA,YAAY;;AAEZ;EACE,WAAW;EACX,gBAAgB;;EAEhB,aAAa;EACb,sBAAsB;EACtB,SAAS;;EAET,cAAc;AAChB;;AAEA,oBAAoB;;AAEpB;EACE,aAAa;EACb,sBAAsB;EACtB,SAAS;EACT,mBAAmB;EACnB,kBAAkB;EAClB,aAAa;AACf;;AAEA;EACE,aAAa;EACb,sBAAsB;EACtB,mBAAmB;AACrB;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,SAAS;AACX;;AAEA;EACE,WAAW;EACX,aAAa;EACb,kCAAkC;EAClC,mBAAmB;EACnB,SAAS;AACX;;AAEA;EACE,aAAa;EACb,aAAa;;EAEb,aAAa;EACb,sBAAsB;EACtB,uBAAuB;EACvB,mBAAmB;EACnB,WAAW;AACb;;AAEA;EACE,iBAAiB;AACnB;;AAEA;EACE,eAAe;AACjB;;AAEA;EACE,eAAe;AACjB;;AAEA;EACE,iBAAiB;AACnB;;AAEA;EACE,eAAe;AACjB;;AAEA;EACE,eAAe;AACjB;;AAEA;EACE,eAAe;AACjB;;AAEA,oBAAoB;AACpB;EACE,aAAa;EACb,aAAa;EACb,sBAAsB;EACtB,WAAW;AACb;;AAEA;EACE,eAAe;EACf,aAAa;EACb,uCAAuC;EACvC,sBAAsB;EACtB,SAAS;EACT,kBAAkB;EAClB,qBAAqB;;EAErB,qBAAqB;EACrB,gCAAgC;AAClC;;AAEA;EACE,aAAa;EACb,sBAAsB;EACtB,mBAAmB;AACrB;;AAEA,mBAAmB;AACnB;EACE,WAAW;EACX,aAAa;EACb,kCAAkC;EAClC,mBAAmB;EACnB,SAAS;AACX;;AAEA;EACE,aAAa;;EAEb,aAAa;EACb,sBAAsB;EACtB,uBAAuB;EACvB,mBAAmB;EACnB,WAAW;AACb;;AAEA,WAAW;AACX;EACE,eAAe;EACf,kBAAkB;AACpB;;AAEA;EACE,kBAAkB;AACpB;;AAEA;EACE,qBAAqB;AACvB;;AAEA,UAAU;AACV;;;EAGE,mBAAmB;EACnB,yBAAyB;EACzB,6BAA6B;AAC/B","sourcesContent":["* {\n  margin: 0;\n  padding: 0;\n  box-sizing: border-b